import {possibleInput} from "../states/constants.js";
import onBackspace from "../services/onBackspace.js";
import onInput from "../services/onInput.js";
import onEquals from "../services/onEquals.js";
export default function keyboardController() {
  document.addEventListener("keydown", (evt) => {
    if (possibleInput.includes(evt.key))
      onInput(evt.key);
    if (evt.key === "Backspace")
      onBackspace();
    if (evt.key === "Enter")
      onEquals();
  });
}
