import {possibleInput} from "../states/constants.js";
import onBackspace from "../services/onBackspace.js";
import onClear from "../services/onClear.js";
import onInput from "../services/onInput.js";
import onEquals from "../services/onEquals.js";
export default function buttonController() {
  document.addEventListener("click", (evt) => {
    const target = evt.target;
    const value = target.getAttribute("data-value");
    if (!target.matches("[data-value]"))
      return;
    if (possibleInput.includes(value))
      onInput(value);
    if (value === "âŒ«")
      onBackspace();
    if (value === "C")
      onClear();
    if (value === "=")
      onEquals();
  });
}
