import * as calcDisplayState from "../states/calculatorDisplayState.js";
import {operators} from "../states/constants.js";
import * as resultState from "../states/resultState.js";
import {calcInput} from "./elements.js";
import {resultDisplay} from "./elements.js";
import {evaluate} from "./evaluate.js";
export default function onBackspace() {
  let currentState = calcDisplayState.getState();
  calcDisplayState.setState(currentState.replace(/.$/, ""));
  if (calcDisplayState.getState() == "")
    calcDisplayState.setState("0");
  if (operators.includes(calcDisplayState.getState()[calcDisplayState.getState().length - 1]))
    resultState.setState(evaluate(calcDisplayState.getState().replace(/.$/, "")));
  else
    resultState.setState(evaluate(calcDisplayState.getState()));
  resultDisplay.innerHTML = resultState.getState() ? resultState.getState().toString() : "";
  calcInput.innerHTML = calcDisplayState.getState();
}
